import { React, Component} from 'react'
import { ListGroupItem } from 'reactstrap'

export default class NewListItem extends Component {
    state = {
        checked: null
    }

    handleChange = (event) => {
        this.props.getData(this.props.item.name, event.target.checked)
        this.setState({checked: event.target.checked})
    }

    render() {
        const key = `item${Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15)}`

        return (
            <ListGroupItem key={key}>
                <input type="checkbox" id={key} onChange={this.handleChange} checked={this.state.checked}></input>
                <label htmlFor={key} style={{marginBottom: "0", marginLeft: "5px"}}>{this.props.item.name}</label>
            </ListGroupItem>
        )
    }
}