
import { Component, React } from 'react'
import { Collapse, Button, ListGroup, ListGroupItem } from 'reactstrap'

export default class Main extends Component{

    state = {
        isOpen: false,
    }

    setIsOpen = () => {
        this.setState({isOpen: !this.state.isOpen})
    }

    showData = () => {
        const key = `item${Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15)}`
        return this.props.data.map(item => {
            console.log(item.name)
            return (
                <ListGroupItem>
                    <input type="checkbox" id={key}></input>
                    <label htmlFor={key} style={{marginBottom: "0", marginLeft: "5px"}}>{item.name}</label>
                </ListGroupItem>
            )
        })
    }

    render() {
        if (this.props.data.length === 0) {
            return (<></>)
        }
        const qwe = this.showData()
        return (
            <>
                <div className="list">
                <h3 onClick={this.setIsOpen}>New List</h3>
                
                <Collapse isOpen={this.state.isOpen}>
                    <ListGroup>

                        {qwe}

                        <ListGroupItem>
                            <Button onClick={this.setIsOpen}>Свернуть</Button>
                        </ListGroupItem>
                    </ListGroup>
                </Collapse>

                </div>
            </>
        )
    }

}

