import { React, Component } from 'react'
import { Button } from 'reactstrap'

import NewListGroup from './NewListGroup'


export default class NewList extends Component{


    vegetables = [{name: 'Tomaten'}, {name: 'Gurken'}]

    fruits = [{name: 'Apple'}, {name: 'Banana'}]



    render() {
        return (
            <>
                <NewListGroup 
                    getData={this.props.getData}
                    art = {this.vegetables} 
                    artName = {'Овощи'}/>
                <NewListGroup 
                    getData={this.props.getData}
                    art = {this.fruits} 
                    artName = {'Фрукты'}/>
            </>
        )
    }

}

