import { React, Component } from 'react'
import { Collapse, Button, ListGroup, ListGroupItem } from 'reactstrap'
import NewListItem from './NewListItem'

export default class NewListGroup extends Component{

    constructor(props) {
        super(props)

        this.state = {
            isOpen: false
        }
    }

    setIsOpen = () => {
        this.setState({isOpen: !this.state.isOpen})
    }

    // handleChange = (event) => {
    //     this.state.
    // }

    renderItemList = () => {
        return this.props.art.map((item, i) => {
            return (
                <NewListItem item={item} getData={this.props.getData}></NewListItem>
            )
        })
    }

    render() {
        const itemList = this.renderItemList()
        return (
            <>
                <div className="list">
                    <h3 onClick={this.setIsOpen}>{this.props.artName}</h3>
    
                    <Collapse isOpen={this.state.isOpen}>
                        <ListGroup>
    
                            {itemList}
    
                            <ListGroupItem>
                                <Button onClick={this.setIsOpen}>Свернуть</Button>
                            </ListGroupItem>
                        </ListGroup>
                    </Collapse>
                </div>
    
            </>
        )
    }

}

